/* basic styling */
html {
  box-sizing: border-box;
}

body {
  font-size:15px;
  padding: 0;
  margin: 0;
  box-sizing: border-box;
  background: -webkit-gradient(linear, left top, right top, from(#952F29),color-stop(50%, #2C1703));
  background: -webkit-linear-gradient(left, #952F29 0%,#2C1703 50%);
  background: linear-gradient(90deg, #952F29 0%,#2C1703 50%);
  width: 100vw;
}

h2 {
  margin: 0;
  font-size: 4rem;
  font-weight: 700;
  letter-spacing: -0.30rem;
  font-family: 'Roboto Slab', sans-serif;
  opacity: 1.0;
}

i {
  color: #770506;
}

p {
  opacity: 0.0;
  margin-bottom: 0;
  margin-top: -6rem;
  -webkit-transition: opacity 1.2s ease-in-out, margin-top 0.8s ease-in-out;
  transition: opacity 1.2s ease-in-out, margin-top 0.8s ease-in-out;
  font-size: 1rem;
  line-height: 1.5rem;
  padding: 1.3rem 1rem 0px 4rem;
  font-family: 'Roboto';
}

a {
  text-decoration: none;
}

/* text clipping for heading*/

.text {
  padding-left: 4rem;
  -webkit-text-fill-color:transparent;
  top: -10rem;
  height: auto;
  background-position-y: -10.2rem;
  -webkit-transition: background-position-y 0.8s ease-in-out;
  transition: background-position-y 0.8s ease-in-out;
}

/* sections */

.container {
  min-height: 16rem;
}

.box {
  position: relative;
  top: -1.2rem;
  background-position-y: -4rem;
  -webkit-transition: box-shadow 1.2s ease-in-out, margin-top 0.8s ease-in-out, height 0.8s ease-in-out;
  transition: box-shadow 1.2s ease-in-out, margin-top 0.8s ease-in-out, height 0.8s ease-in-out;
  padding-bottom: 1rem;
}

.container {
  margin-top: -10.2rem;
  display:flex;
  -webkit-transition: margin-top 0.8s ease-in-out;
  transition: margin-top 0.8s ease-in-out;
}

/* movement */

.box:hover, 
.container:hover, 
.box:hover p, 
.boxHover,
.boxHover p {
  margin-top: 0rem;
  height:auto;
}

.box:hover p,
.boxHover p {
  opacity: 1.0;
}

.box:hover,
.boxHover {
  box-shadow:2px 2px 3px rgba(10, 10, 10, 0.1);
  z-index:200;
  min-height: 16rem;
}

.containerHover {
  margin-top: 0rem;
  z-index:200;
  min-height: 16rem;
}

section:hover .text,
.textHover {
  background-position-y: 0rem;
}

/* flexbox sections */

.link {
  flex-grow:1;
  flex-basis: 15%;
}

.text-body {
  flex-grow: 6;
  flex-basis: 85%;
}

/* title text */


.title {

  color: #770506;
  background: #fff;
  font-size: 2rem;
  font-weight: 700;
  font-family: 'Roboto Slab', serif;
  padding: 0 1.3rem;
  margin: 2rem -12.5rem;
  -webkit-transform: rotate(90deg);
  transform: rotate(90deg);
  float: left;
  z-index: 10000;
  position:fixed;
  box-shadow: 3px 3px 4px rgba(10, 10, 10, 0.1);
}

/* this class acts only to keep the sections separate from the title text, without .content, .title does not work */


#content {
  height: 10rem;
}

/* 740px width breakpoint*/
@media (max-width: 740px) {
  .container{ 
    flex-direction: column;
    height:16rem;
    margin-top: -3.2rem;
    }

  h2 {
    font-size: 2rem;
    letter-spacing: -0.15rem;
  }

  .boxHover, .box { 
    background-position-y: -2rem;
    top: -0.7rem;
    z-index:200;
    height:16rem;
  }

  .title {
    font-size: 1.5rem;
    margin: -1rem -9.6rem;
  }
}
